@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=Allison&family=EB+Garamond&display=swap');

@layer base {
  html {
    font-family: 'EB Garamond', 'Garamond', serif;
    background-color: #05061a;
    color: #fff;
  }
  .header {
    background-image: url('./images/portheader.svg');
    height: 600px;
    @apply h-[600px] bg-cover bg-bottom
  }
  @media screen and (max-width: 700px) {
    .header {
      height: 300px;
    }
  }
  p {
    font-weight: 100;
  }
  h1 {
    font-family: 'Allison', sans-serif;
    font-size: 40px;
  }
  @media screen and (max-width: 700px) {
    .container {
      @apply px-3
    }
  }
  .swirl-animation {
    position: relative;
    display: inline-block;
    width: 200px;
    height: 50px;
    line-height: 60px;
    text-align: center;
    background-color: transparent;
    cursor: pointer;
    letter-spacing: 0.045em;
  }
  .swirl-animation svg {
    position: absolute;
    top: 0;
    left: 0;
  }
  .swirl-animation rect {
    stroke-width: 1px;
    stroke-dasharray: 353, 0;
    stroke-dashoffset: 0;
    -webkit-transition: all 600ms ease;
    transition: all 600ms ease;
  }
  .swirl-animation:hover svg rect {
    stroke-width: 2px;
    stroke-dasharray: 123, 543;
    stroke-dashoffset: 437;
  }
  .alt-port:nth-child(even) .portimg {
    @apply md:order-2
  }
  .alt-port:nth-child(even) .port-text {
    @apply md:order-1
  }
  .alt-port:nth-child(odd) .portimg {
    @apply md:order-1
  }
  .alt-port:nth-child(odd) .port-text{
    @apply md:order-2
  }
  .navi {
    backdrop-filter: blur(25px) saturate(1.1) brightness(0.5);
  }
}